import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter, Route, Switch } from 'react-router-dom';
import {Login} from './components/Login';
ReactDOM.render(
    <HashRouter>
		<Switch>
		   <Route path="/" component={Login}></Route>
		</Switch>
	</HashRouter>
    ,
	document.getElementById('app')
);
